<template>
  <div class="priority-page">
    <h2 class="priority-title">Ïö∞ÏÑ†ÏàúÏúÑ ÏäπÏù∏ ÏöîÏ≤≠ Î™©Î°ù</h2>

    <div class="priority-filters">
      <input
        v-model="keyword"
        class="priority-input"
        placeholder="Ïù¥Î¶Ñ/Î≥¥Ìò∏Ïûê/Í∏∞Í¥Ä Í≤ÄÏÉâ"
        @keyup.enter="searchList"
      />

      <select v-model="state" class="priority-select" @change="searchList">
        <option value="">Ï†ÑÏ≤¥</option>
        <option value="BA1">ÏöîÏ≤≠</option>
        <option value="BA2">ÏäπÏù∏</option>
        <option value="BA3">Î∞òÎ†§</option>
      </select>

      <select v-model="orderBy" class="priority-select" @change="searchList">
        <option value="latest">ÏµúÏã†Ïàú</option>
        <option value="oldest">Ïò§ÎûòÎêúÏàú</option>
        <option value="name">Ïù¥Î¶ÑÏàú</option>
        <option value="priority">Ïö∞ÏÑ†ÏàúÏúÑÏàú</option>
      </select>
    </div>

    <div class="priority-card">
      <!-- üîπ Î°úÎî© Ï§ëÏùº ÎïåÎßå ÌëúÏãú -->
      <div v-if="loading" class="priority-loading">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>

      <!-- üîπ Î°úÎî©Ïù¥ ÎÅùÎÇ¨ÏùÑ ÎïåÎßå ÌÖåÏù¥Î∏î ÌëúÏãú -->
      <table v-else class="priority-table">
        <thead>
          <tr>
            <th>ÏäπÏù∏ÏΩîÎìú</th>
            <th>Ïù¥Î¶Ñ</th>
            <th>Ïû•Ïï†Ïú†Ìòï</th>
            <th>Î≥¥Ìò∏Ïûê</th>
            <th>Îã¥ÎãπÏûê</th>
            <th v-if="isOrgVisible">Í∏∞Í¥Ä</th>
            <th>ÏÉÅÎã¥Í∏∞Î°ù</th>
            <th>Ïö∞ÏÑ†ÏàúÏúÑ</th>
            <th>ÏÉÅÌÉú</th>
            <th>Ï≤òÎ¶¨Ïùº</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in list"
            :key="item.approval_code"
            @click="onRowClick(item)"
            class="priority-row"
          >
            <td>{{ item.approval_code }}</td>
            <td>{{ item.child_name }}</td>
            <td>{{ item.disability_type }}</td>
            <td>{{ item.parent_name }}</td>
            <td>{{ item.manager_name }}</td>
            <td v-if="isOrgVisible">{{ item.org_name }}</td>
            <td>{{ formatDate(item.counsel_date) }}</td>
            <td>
              <span
                class="priority-chip"
                :class="priorityChipClass(item.priority_level)"
              >
                {{ codeLabel(item.priority_level) }}
              </span>
            </td>
            <td>
              <span class="priority-badge" :class="stateBadgeClass(item.state)">
                {{ codeLabel(item.state) }}
              </span>
            </td>
            <td>
              {{
                item.state === "BA1" || !item.approval_date
                  ? "-"
                  : formatDate(item.approval_date)
              }}
            </td>
          </tr>

          <!-- üîπ Î°úÎî©Ïù¥ ÎÅùÎÇ¨Í≥† + Îç∞Ïù¥ÌÑ∞ ÏóÜÏùÑ ÎïåÎßå Î©îÏãúÏßÄ -->
          <tr v-if="list.length === 0">
            <td class="priority-empty" :colspan="isOrgVisible ? 10 : 9">
              Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏäµÎãàÎã§.
            </td>
          </tr>
        </tbody>
      </table>

      <!-- üîπ ÌéòÏù¥ÏßïÎèÑ Î°úÎî© ÎÅùÎÇú Îí§ÏóêÎßå -->
      <div v-if="!loading && totalPages > 1" class="priority-pagination">
        <button
          class="priority-page-btn"
          :disabled="page === 1"
          @click="changePage(page - 1)"
        >
          Ïù¥Ï†Ñ
        </button>

        <span class="priority-page-info">
          {{ page }} / {{ totalPages }} (Ï¥ù {{ totalCount }}Í±¥)
        </span>

        <button
          class="priority-page-btn"
          :disabled="page === totalPages"
          @click="changePage(page + 1)"
        >
          Îã§Ïùå
        </button>
      </div>

      <!-- üîπ Î∞òÎ†§ÏÇ¨Ïú† Î™®Îã¨ -->
      <div
        v-if="showRejectModal"
        class="priority-modal-backdrop"
        @click.self="closeRejectModal"
      >
        <div class="priority-modal">
          <h3 class="priority-modal-title">Ïù¥Ï†Ñ Î∞òÎ†§ ÏÇ¨Ïú†</h3>

          <div class="priority-modal-section">
            <div class="priority-modal-label">Î∞òÎ†§ ÏÇ¨Ïú†</div>
            <div class="priority-modal-reason">
              {{ rejectModalData.reason }}
            </div>
          </div>

          <div class="priority-modal-section">
            <div class="priority-modal-label">ÏµúÏã† ÏäπÏù∏ ÏÉÅÌÉú</div>
            <div class="priority-modal-text">
              <template v-if="rejectModalData.newestState === 'BA1'">
                Ïù¥ Í±¥ÏùÄ ÌòÑÏû¨
                <strong>Ïû¨ÏäπÏù∏ ÏöîÏ≤≠(ÎåÄÍ∏∞)</strong>
                ÏÉÅÌÉúÏûÖÎãàÎã§.<br />
                Ïû¨ÏäπÏù∏ ÏäπÏù∏ÏΩîÎìú:
              </template>

              <template v-else-if="rejectModalData.newestState === 'BA2'">
                Ïù¥ Í±¥ÏùÄ ÌòÑÏû¨
                <strong>ÏäπÏù∏ ÏôÑÎ£åÎêú Í±¥</strong>
                ÏûÖÎãàÎã§.<br />
                ÏµúÏ¢Ö ÏäπÏù∏ÏΩîÎìú:
              </template>

              <template v-else-if="rejectModalData.newestState === 'BA3'">
                Ïù¥ Í±¥ÏùÄ ÌòÑÏû¨ÎèÑ
                <strong>Î∞òÎ†§ ÏÉÅÌÉú</strong>
                ÏûÖÎãàÎã§.<br />
                ÏµúÏã† ÏäπÏù∏ÏΩîÎìú:
              </template>

              <template v-else>
                Ïù¥ Í±¥ÏóêÎäî Ïù¥ÌõÑ ÏäπÏù∏ Ïù¥Î†•Ïù¥ ÏûàÏäµÎãàÎã§.<br />
                ÏµúÏã† ÏäπÏù∏ÏΩîÎìú:
              </template>

              <strong>{{ rejectModalData.newestApprovalCode || "-" }}</strong>
            </div>
          </div>

          <div class="priority-modal-actions">
            <button class="priority-modal-btn" @click="closeRejectModal">
              Îã´Í∏∞
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
import { useAuthStore } from "@/store/authLogin";

const router = useRouter();
const auth = useAuthStore();

const list = ref([]);

const page = ref(1);
const pageSize = ref(10);
const totalCount = ref(0);
const loading = ref(false);

const totalPages = computed(() =>
  totalCount.value > 0 ? Math.ceil(totalCount.value / pageSize.value) : 1
);

const keyword = ref("");
const state = ref("");
const orderBy = ref("latest"); // ÏµúÏã†Ïàú Í∏∞Î≥∏

const showRejectModal = ref(false);
const rejectModalData = ref({
  reason: "",
  newestApprovalCode: "",
  newestState: "",
});

const detailRole = computed(() => {
  return isSystemAdmin.value ? 4 : 3;
});

// üîπ Í≥µÌÜµ: Ïú†Ï†Ä role, loginId Í≥ÑÏÇ∞
const userRole = computed(() => {
  return (
    auth.role || // Ïòà: 'AA3', 'AA4'
    (auth.user && auth.user.roleCode) ||
    ""
  );
});

// üîπ Î°úÍ∑∏Ïù∏ ÏïÑÏù¥Îîî
const loginId = computed(() => {
  return (
    auth.userId ||
    auth.loginId ||
    auth.id ||
    (auth.user && auth.user.userId) ||
    ""
  );
});

// üîπ Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê Ïó¨Î∂Ä (AA3)
const isOrgManager = computed(() => userRole.value === "AA3");

// üîπ ÏãúÏä§ÌÖú Í¥ÄÎ¶¨Ïûê Ïó¨Î∂Ä (AA4)
const isSystemAdmin = computed(() => userRole.value === "AA4");

// üîπ Ïù¥ ÌéòÏù¥ÏßÄ Ï†ëÍ∑º Í∞ÄÎä•ÌïúÏßÄ (AA3 ÎòêÎäî AA4)
const canViewPriorityPage = computed(
  () => isOrgManager.value || isSystemAdmin.value
);

// üîπ Í∏∞Í¥Ä Ïª¨Îüº ÌëúÏãú Ïó¨Î∂Ä (Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê AA3Ïù¥Î©¥ Ïà®ÍπÄ)
const isOrgVisible = computed(() => !isOrgManager.value);

// Í≥µÌÜµÏΩîÎìú Îß§Ìïë
const CODE_LABEL_MAP = {
  // ÏöîÏ≤≠ ÏÉÅÌÉú(BA)
  BA1: "ÏöîÏ≤≠",
  BA2: "ÏäπÏù∏",
  BA3: "Î∞òÎ†§",

  // Ïö∞ÏÑ†ÏàúÏúÑ Ïú†Ìòï(BB)
  BB1: "Í∏¥Í∏â",
  BB2: "Ï§ëÏ†ê",
  BB3: "Ï§ÄÎπÑ",
};

// Í≤ÄÏÉâ Î∞è ÌïÑÌÑ∞ÎßÅ Ï†ÅÏö©
function searchList() {
  page.value = 1; // Í≤ÄÏÉâÌïòÎ©¥ ÌéòÏù¥ÏßÄ Ï¥àÍ∏∞Ìôî
  loadList();
}

// ÏΩîÎìú ‚Üí ÎùºÎ≤® Î≥ÄÌôò
function codeLabel(code) {
  if (!code) return "";
  return CODE_LABEL_MAP[code] || code;
}

// Ïö∞ÏÑ†ÏàúÏúÑ Ïπ© ÏÉâÏÉÅ
function priorityChipClass(level) {
  switch (level) {
    case "BB1": // Í∏¥Í∏â
      return "priority-chip-danger";
    case "BB2": // Ï§ëÏ†ê
      return "priority-chip-warning";
    case "BB3": // Ï§ÄÎπÑ
      return "priority-chip-info";
    default:
      return "priority-chip-default";
  }
}

// ÏÉÅÌÉú Î±ÉÏßÄ ÏÉâÏÉÅ
function stateBadgeClass(stateCode) {
  switch (stateCode) {
    case "BA1": // ÏöîÏ≤≠
      return "priority-badge-request";
    case "BA2": // ÏäπÏù∏
      return "priority-badge-approve";
    case "BA3": // Î∞òÎ†§
      return "priority-badge-reject";
    default:
      return "priority-badge-default";
  }
}

function formatDate(value) {
  if (!value) return "";
  const s = String(value);
  return s.length >= 10 ? s.slice(0, 10) : s;
}

// üîπ Î¶¨Ïä§Ìä∏ Î°úÎî© (ÏÑúÎ≤Ñ ÌéòÏù¥Ïßï)
async function loadList() {
  // üî∏ AA3 / AA4Îßå ÏÑúÎ≤Ñ Ìò∏Ï∂ú
  if (!canViewPriorityPage.value) return;

  loading.value = true;
  try {
    const res = await axios.get("/api/approvals/priority", {
      params: {
        page: page.value,
        size: pageSize.value,
        keyword: keyword.value,
        state: state.value,
        orderBy: orderBy.value,
        loginId: loginId.value, // üîπ Í∏∞Í¥Ä ÌïÑÌÑ∞ Í∏∞Ï§Ä ÏïÑÏù¥Îîî
        role: userRole.value, // üîπ Ïó≠Ìï†(AA3 / AA4)
      },
    });

    const payload = res.data?.data || {};
    const rows = Array.isArray(payload.rows) ? payload.rows : [];

    list.value = rows;
    totalCount.value = payload.totalCount ?? 0;
  } catch (err) {
    console.error("[PriorityApproval] loadList error:", err);
    list.value = [];
    totalCount.value = 0;
  } finally {
    loading.value = false;
  }
}

// ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω
function changePage(nextPage) {
  if (nextPage < 1) return;
  if (nextPage > totalPages.value) return;
  if (nextPage === page.value) return;

  page.value = nextPage;
  loadList();
}

// Î™®Îã¨ Îã´Í∏∞
function closeRejectModal() {
  showRejectModal.value = false;
}

// Ìñâ ÌÅ¥Î¶≠ Ï≤òÎ¶¨
function onRowClick(item) {
  // üîπ Ïû¨ÏöîÏ≤≠ Ïù¥Î†•Ïù¥ ÏûàÎäî Ïòõ Î∞òÎ†§Í±¥Îßå Î™®Îã¨
  if (item.state === "BA3" && item.has_newer_request) {
    // newest_approval_code Ïóê Ìï¥ÎãπÌïòÎäî ÏµúÏã† ÌñâÏùÑ ÌòÑÏû¨ Î™©Î°ùÏóêÏÑú Ï∞æÍ∏∞
    const latestRow = list.value.find(
      (row) => row.approval_code === item.newest_approval_code
    );

    const newestState = latestRow?.state || "";

    rejectModalData.value = {
      reason: item.rejection_reason || "(Îì±Î°ùÎêú Î∞òÎ†§ ÏÇ¨Ïú†Í∞Ä ÏóÜÏäµÎãàÎã§.)",
      newestApprovalCode: item.newest_approval_code || "",
      newestState, // BA1 / BA2 / BA3 ...
    };

    showRejectModal.value = true;
    return;
  }

  // Í∑∏ Ïô∏Îäî Í∏∞Ï°¥ ÏÉÅÏÑ∏ Ïù¥Îèô
  goDetail(item);
}

// ‚úÖ Í∞Å Ìñâ ÌÅ¥Î¶≠ Ïãú ÏÉÅÎã¥ ÏÉÅÏÑ∏Î°ú Ïù¥Îèô
function goDetail(item) {
  router.push({
    name: "counsel-detail",
    params: { submitCode: item.submit_code },
    query: {
      role: detailRole.value, // AA3 -> 3, AA4 -> 4
    },
  });
}

onMounted(() => {
  // üî∏ Ï†ëÍ∑º Í∂åÌïú Ï≤¥ÌÅ¨: Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê + ÏãúÏä§ÌÖú Í¥ÄÎ¶¨ÏûêÎßå
  if (!canViewPriorityPage.value) {
    alert("Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê Î∞è ÏãúÏä§ÌÖú Í¥ÄÎ¶¨ÏûêÎßå Ï†ëÍ∑º Í∞ÄÎä•Ìï©ÎãàÎã§.");
    router.push("/");
    return;
  }
  loadList();
});
</script>

<style scoped>
