  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 4px rgba(15, 23, 42, 0.06);
  padding: 12px 16px;
}

/* ÌÖåÏù¥Î∏î Í∏∞Î≥∏ */
.priority-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;
}

/* Ìó§Îçî */
.priority-table thead th {
  text-align: center;
