  font-weight: 600;
  font-size: 12px;
  color: #6b7280;
  border-bottom: 1px solid #e5e7eb;
  background: #f9fafb;
  white-space: nowrap;
}

/* Î∞îÎîî ÏÖÄ */
.priority-table tbody td {
  padding: 9px 8px;
  border-bottom: 1px solid #f3f4f6;
  color: #374151;
  vertical-align: middle;
  text-align: center;
}

/* Ìñâ hover Ìö®Í≥º */
.priority-row {
  cursor: pointer;
  transition:
    background-color 0.12s ease,
    transform 0.06s ease;
}

.priority-row:hover {
  background: #f3f4ff;
  transform: translateY(-1px);
}

/* Îπà Îç∞Ïù¥ÌÑ∞ */
.priority-empty {
  text-align: center;
  padding: 14px 0;
  color: #9ca3af;
}

/* Ïö∞ÏÑ†ÏàúÏúÑ Ïπ© */
.priority-chip {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 500;
}

/* Ïö∞ÏÑ†ÏàúÏúÑÎ≥Ñ ÏÉâÏÉÅ */
.priority-chip-danger {
  background: #fee2e2;
  color: #b91c1c;
}

.priority-chip-warning {
  background: #fef3c7;
  color: #92400e;
}

.priority-chip-info {
  background: #e0f2fe;
  color: #075985;
}

.priority-chip-default {
  background: #e5e7eb;
  color: #4b5563;
}

/* ÏÉÅÌÉú Î±ÉÏßÄ */
.priority-badge {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 500;
  border: 1px solid transparent;
}

/* ÏöîÏ≤≠/ÏäπÏù∏/Î∞òÎ†§ ÏÉâÏÉÅ */
.priority-badge-request {
  background: #eef2ff;
  border-color: #c7d2fe;
  color: #3730a3;
}

.priority-badge-approve {
  background: #ecfdf5;
  border-color: #bbf7d0;
  color: #166534;
}

.priority-badge-reject {
  background: #fef2f2;
  border-color: #fecaca;
  color: #b91c1c;
}

.priority-badge-default {
  background: #f3f4f6;
  border-color: #e5e7eb;
  color: #4b5563;
}

.priority-loading {
  font-size: 13px;
  color: #6b7280;
  padding: 8px 4px;
}

/* ÌéòÏù¥Ïßï */
.priority-pagination {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 8px;
