}

.priority-modal {
  width: 420px;
  max-width: 90%;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(15, 23, 42, 0.25);
  padding: 20px 22px 16px;
}

.priority-modal-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 12px;
  color: #111827;
}

.priority-modal-section {
  margin-bottom: 12px;
}

.priority-modal-label {
  font-size: 12px;
  font-weight: 600;
