}

.priority-modal-btn {
  padding: 6px 14px;
  border-radius: 999px;
  border: 1px solid #d1d5db;
  background: #111827;
  color: #f9fafb;
  font-size: 12px;
  cursor: pointer;
  transition:
    background-color 0.12s ease,
    transform 0.06s ease,
    box-shadow 0.12s ease;
}

.priority-modal-btn:hover {
  background: #1f2937;
  box-shadow: 0 1px 4px rgba(15, 23, 42, 0.18);
